import { Box, BoxProps, Button, ButtonGroup, Card, FormHelperText, Stack, TextField, Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import "./updateCoupon.css";
import { useLocation, useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { store } from './../../../redux/store';
import Category from "../../../modal/Category";
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import { updateCoupon } from "../../../redux/couponState";
import advNotify, { SccMsg } from "../../../utils/notify_advenced";
import notify from './../../../utils/notify';
import Coupon from './../../../modal/Coupon';
import category from "../../../modal/Category";



function Item(props: BoxProps) {
  const { sx, ...other } = props;
  return (
  <Box
  sx={{
  p: 1,
  m: 1,
  color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
  }}
  {...other}
/>
);
}
function UpdateCoupon(): JSX.Element {
  const location= useLocation();
    const {couponId}= location.state as any;  
    const navigate=useNavigate();
    const [coupon,setCoupon]= useState(new Coupon());

   // TODO: this need to be the min value for client side validition+ TODO: validate amount and price
    var startDate = new Date().toISOString().slice(0, 10); 
    useEffect(()=>{  
    setCoupon(store.getState().CouponState.coupons.find(item=> item.id == couponId))
    },[])


    let handleInputChange = (args:any ) => {
      //const name = e.target.name 
      //const value = e.target.value 
      const { name, value } = args.target ;
      setCoupon({
        ...coupon,
        [name]: value,
      });
    };

    const handleChange = (event: SelectChangeEvent) => {
    coupon.category=event.target.value as string;
  
  };
  

    const {register, handleSubmit, formState: { errors } } = useForm<Coupon>();
    const send =async ()=>{
    console.log(coupon);
    jwtAxios.put(globals.company.updateCoupon,coupon)
    .then((response)=>{
      if (response.status==200){
        advNotify.success(SccMsg.UPDATE_COUPON);
      }else{
        notify.error("Something went wrong");
      }
    })
    .then(()=>{
      store.dispatch(updateCoupon(coupon));
      navigate("/company/companyCoupons",{state:{companyId:0}});
      
    })
    .catch((err)=>{
      console.log(err);
    })
    }
    return (
      <div className="updateCoupon Single" style={{ textAlign: "center" }}>
          <Card sx={{ maxWidth: 800 , border: "1px solid white" }}>
        <Typography className="headLine" variant="h5">
        Update Coupon
          </Typography>
        <hr />
        
      <form onSubmit={handleSubmit(send)}>
      <Box component="form"
        sx={{ display: 'flex', flexWrap: 'wrap' }}>
            <div style={{ width: '100%' }}>
            <Item >
      <FormControl fullWidth >
            <InputLabel id="demo-simple-select-label">Category</InputLabel>
            <Select  {...register("category",{
                required: "Please select a category",
            })}    
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={coupon.category}  name="category"   
            label="Category"
            onChange={handleChange}
            >
                {category.map((item,index)=>
            <MenuItem key={index} value={item}>{item}</MenuItem>
            )}
            </Select>
            </FormControl> 
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.category?.message}
            </FormHelperText>
            </Item>
            </div>
            <div style={{ width: '100%' }}>
            <Item >
            <TextField
            label=" title"
            variant="outlined"
            name="title"
            className="TextBox"
            fullWidth
            {...register("title", {
                required: {
                  value: true,
                  message: "Please enter your title",
                },
              })}
            value={coupon.title}
            onChange={handleInputChange}
          ></TextField>
          </Item>
          </div>
          <div style={{ width: '100%' }}>
        <Item >
        <TextField  
            label="description"
            variant="outlined"
            name="description"
            className="TextBox"
            fullWidth 
            {...register("description", {
                required: {
                value: true,
                message: "Please enter your description",
            },
            })}
              value={coupon.description}
              onChange={handleInputChange}
        ></TextField> 
        </Item>
        </div>
        <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.description?.message}
            </FormHelperText>
            <div style={{ width: '100%' }}>
    <Box
        sx={{
        display: 'grid',
        columnGap: 3,
        rowGap: 1,
        gridTemplateColumns: 'repeat(2, 1fr)',
        }}
        >

        <Item >  
            <TextField
            type={"number"}
            name="amount"
            label="amount"
            className="TextBox"
            fullWidth 
            {...register("amount",{
                required: {
                value: true,
                message: "Please enter your amount",
                }
            })}
              value={coupon.amount}
              onChange={handleInputChange}
            ></TextField>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.amount?.message}
            </FormHelperText>
            </Item>
            <Item >
            <TextField
            type={"number"}
            name="price"
            label="price"
            className="TextBox"
            fullWidth 
            {...register("price", {
                required: {
                value: true,
                message: "Please enter your price",
                },
            })}
              value={coupon.price}
              onChange={handleInputChange}
            ></TextField>
              </Item>
        </Box>
        </div>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.price?.message}
            </FormHelperText>
            <div style={{ width: '100%' }}>
            <Item >
            <TextField
            name="image"
            label="image"
            className="TextBox"
            fullWidth
            {...register("image", {
                required: {
                value: true,
                message: "Please enter your image",
                },
            })}
              value={coupon.image}
              onChange={handleInputChange}
            ></TextField>
              </Item>
            </div>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.image?.message}
            </FormHelperText>
            <div style={{ width: '100%' }}>
        <Box
        sx={{
        display: 'grid',
        columnGap: 3,
        rowGap: 1,
        gridTemplateColumns: 'repeat(2, 1fr)',
        }}
        >
           <Item >
            <TextField
            type={"date"}
            name="startDate"
            fullWidth
            label="start Date"
            className="TextBox "
            {...register("startDate", {
                required: {
                value: true,
                message: "Please enter your start date",
                },
            })}
            value={coupon.startDate}
            onChange={handleInputChange}
            />
            </Item>
            <Item >
            <TextField
            type={"date"}
            name="endDate"
            label="End Date"
            className="TextBox"
            fullWidth 
            {...register("endDate", {
                required: {
                value: true,
                message: "Please enter your end date",
                },
            })}  
            value={coupon.endDate}
            onChange={handleInputChange}
            ></TextField>
            </Item>
        </Box>
    </div>
    </Box>
          <ButtonGroup variant="contained" fullWidth>
            <Button type="submit" color="primary" >
              Send
            </Button>
            <Button type="reset" color="secondary">
              reset
            </Button>
          </ButtonGroup>
        </form>
        </Card>
      </div>
    );
}

export default UpdateCoupon;
