import "./updateCustomer.css";
import { useLocation, useNavigate } from 'react-router-dom';
import Customer from "../../../modal/Customer";

import { useForm } from "react-hook-form";
import { Button, ButtonGroup, FormControl, FormControlLabel, FormHelperText, FormLabel, RadioGroup, TextField, Typography } from "@mui/material";
import { SyntheticEvent, useEffect, useState } from "react";
import { store } from "../../../redux/store";
import jwtAxios from "../../../utils/JWTaxios";
import globals from './../../../utils/global';
import advNotify, { SccMsg } from "../../../utils/notify_advenced";
import notify from './../../../utils/notify';
import { updateCustomer } from "../../../redux/customerState";
function UpdateCustomer(): JSX.Element {
    const location= useLocation();
    const navigate=useNavigate();
    const {customerId}= location.state as any;
   const[customer,setCustomer]=useState(new Customer());
   const {register, handleSubmit, formState: { errors } } = useForm<Customer>();
   const send=()=>{
       jwtAxios.put(globals.admin.updateCustomer,customer)
       .then((response)=>{
           if(response.status == 200) {
               advNotify.success(SccMsg.UPDATE_CUSTOMER)
           }else{
               notify.error("somehing went wrong");
           }
       })
       .then(()=>{
           store.dispatch(updateCustomer(customer));
           navigate("/admin/getAllCustomers");
       })
       .catch((err)=>{
           console.log(err);
           advNotify.error(err);
       })

    }
    useEffect(()=>{
      setCustomer(store.getState().customerState.customers.find(item=>item.id==customerId))
    },[])
    console.log(customer);
    const handleInputChange = (args:any) => {
    //const name = e.target.name 
    //const value = e.target.value 
    const { name, value } = args.target ;
    setCustomer({
      ...customer,
      [name]: value,
    });
  };
  
    return (
        <div className="updateCustomer BoxStyle" style={{textAlign: 'center'}}>
			  <Typography className="headLine" variant="h3">
          Update Customer
        </Typography>
        <hr />
        <form onSubmit={handleSubmit(send)}>
          <TextField
            label="First Name"
            variant="outlined"
            name="firstName"
            className="TextBox"
            fullWidth
            {...register("firstName", {
                required: {
                  value: true,
                  message: "Please enter your first name",
                },
              })}
              value={customer.firstName}
              onChange={handleInputChange}
          ></TextField>
          <br />
          <br />
          <TextField
            label="last Name"
            variant="outlined"
            name="lastName"
            className="TextBox"
            fullWidth
            {...register("lastName", {
                required: {
                  value: true,
                  message: "Please enter your last name",
                }
              })}
              value={customer.lastName}
            onChange={handleInputChange}
          ></TextField>
          <br />
          <br />
          <TextField
            label="email"
            name="email"
            variant="outlined"
            className="TextBox"
            fullWidth
            {...register("email", {
              required: {
                value: true,
                message: "Please enter your email",
              },
            })} 
            value={customer.email}
            onChange={handleInputChange}
            ></TextField>
          {errors.email?.message}
          <br /> <br />
          <br /> <br />
          <ButtonGroup variant="contained" fullWidth>
            <Button style={{margin:"10px"}} type="submit" color="primary" >
              Send
            </Button>
            <Button style={{margin:"10px"}} type="reset" color="secondary">
              reset
            </Button>
          </ButtonGroup>
        </form>
        </div>
    );
}

export default UpdateCustomer;
