
import { NavLink, useNavigate } from "react-router-dom";
import "./myHeader.css";


import Switch from '@mui/material/Switch';

import { useEffect, useState } from "react";

import { ButtonGroup ,Button, AppBar, Box, IconButton, Toolbar, Typography, Divider, ListItemIcon, Menu, MenuItem} from '@mui/material';
import HomeIcon from '@mui/icons-material/Home';
import MenuIcon from '@mui/icons-material/Menu';
import { Home, Logout } from "@mui/icons-material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useDispatch } from "react-redux";
import { userLogout } from "../../../../redux/authState";
import { store } from "../../../../redux/store";
import { color } from '@mui/system';
function MyHeader({check,change}:any): JSX.Element {

 

    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const open = Boolean(anchorEl);

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        // console.log(store.getState().authState);
        const newType=store.getState().authState.userType;
        // console.log(type);
        setType(newType);
        if(type==='ADMIN'){
            setMenuOptions(adminOptions);
            setMenuLinks(adminLinks);
        }else if(type==='COMPANY'){
            setMenuOptions(companyOptions);
            setMenuLinks(companyLinks);
        }else if(type==='CUSTOMER'){
            setMenuOptions(customerOptions);
            setMenuLinks(customerLinks);
        }else{
            setMenuOptions([]);
            setMenuLinks([]);
        }
        // console.log(menuOptions,menuLinks);
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
      setAnchorEl(null);
    };

    // -------------------
    const navigate=useNavigate();
    const [type, setType]= useState('');
    const [menuOptions,setMenuOptions]=useState([]);
    const [menuLinks, setMenuLinks]=useState([]);    
    // const [isLogged,setIsLogged]=useState(store.getState().authState.loggedIn);
    useEffect(()=>{
        // console.log(isLogged);

    },[])
    const dispatch=useDispatch();
    const logout=()=>{
        dispatch(userLogout());
        handleClose();
        navigate("/");
    }

    const adminOptions=["All Companies","Add Company","All Customers","Add Customer",];
    const adminLinks=["/admin/getAllCompanies","/admin/addCompany","/admin/getAllCustomers","/admin/addCustomer",];

    const companyOptions=["Details","All Coupons","Add Coupon",];
    const companyLinks=["/company/companyDetails","/company/companyCoupons","/company/addCoupon",];

    const customerOptions=["Details","My Coupons",];
    const customerLinks=["/customer/customerDetails","/customer/customerCoupons",];

    const options=[
        "All Companies","Add Company","All Customers","Add Customer",
        "Details","All Coupons","Add Coupon",
        "Details","My Coupons"
    ];
    const links=[
        "/admin/getAllCompanies","/admin/addCompany","/admin/getAllCustomers","/admin/addCustomer",
        "/company/companyDetails","/company/companyCoupons","/company/addCoupon",
        "/customer/customerDetails","/customer/customerCoupons"
    ];

    return (
        <div className="myHeader">
        <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
            <Toolbar>
            <IconButton
                color="inherit"
                onClick={() => {
                navigate(-1);
                }}
                >
                <ArrowBackIcon />
                </IconButton>
                <IconButton
                id="basic-button"
                aria-controls={open ? "basic-menu" : undefined}
                aria-haspopup="true"
                aria-expanded={open ? "true" : undefined}
                onClick={handleClick}
                color="inherit"
                >
                <MenuIcon />
                </IconButton>
                <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                "aria-labelledby": "basic-button",
                }}
            >
                {store.getState().authState.userType === "ADMIN" &&
                adminOptions.map((option, index) => (
                    <MenuItem
                    key={index}
                    onClick={() => {
                        navigate(adminLinks[index]);
                        handleClose();
                    }}
                    >
                    {option}
                    </MenuItem>
                ))}

                {store.getState().authState.userType === "COMPANY" &&
                companyOptions.map((option, index) => (
                <MenuItem
                    key={index}
                    onClick={() => {
                    navigate(companyLinks[index],{state:{companyId:0},});
                    handleClose();
                    }}
                    >
                    {option}
                    </MenuItem>
                ))}

                {store.getState().authState.userType === "CUSTOMER" &&
                customerOptions.map((option, index) => (
                    <MenuItem
                    key={index}
                    onClick={() => {
                    navigate(customerLinks[index],{state:{customerId:0}},);
                    handleClose();
                    }}
                    >
                    {option}
                    </MenuItem>
                ))}

                <Divider />

                <MenuItem onClick={logout}>
                    Logout{" "}
                    <ListItemIcon>
                    <Logout fontSize="small" />
                    </ListItemIcon>
                </MenuItem>
                </Menu>
                <IconButton
                color="inherit"
                onClick={() => {
                navigate("/");
                }}
                >
                <HomeIcon />
                </IconButton>
                <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                coupons
                </Typography>
                {(store.getState().authState.isLoggedIn != true)&&<Button
                color="inherit"
                onClick={() => {
                navigate("/login");
                }}
                >
                Login
                </Button>}
                <Switch
                defaultChecked
                color="default"
                inputProps={{ "aria-label": "checkbox with default color" }}
                onChange={change}
                checked={check}
            />
            </Toolbar>
        </AppBar>
        </Box>
    </div>
    );
}

export default MyHeader;
