import "./companyCouponsByMaxPrice.css";
import { useLocation, useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { store } from "../../../redux/store";
import { Box, Button, Typography } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import SingleCoupon from "../singleCoupon/singleCoupon";

function CompanyCouponsByMaxPrice(): JSX.Element {
    const location=useLocation();
    const navigate=useNavigate();
    const{maxPrice}=location.state as any;
    const[coupons,setCoupons]=useState<Coupon[]>([]);
    useEffect(()=>{
       setCoupons(store.getState().CouponState.coupons.filter(item=>item.price<=maxPrice))
    },[])
    console.log(coupons);
    
    return (
        <div className="companyCouponsByMaxPrice">
		  <Box sx={{ p: '2px 4px'  }}>
            <Typography variant="h4" color={"secondary"} >All Coupons up to {maxPrice}$ price</Typography>
            <Button  variant="contained" color="info" startIcon={<ArrowBackIcon/>} onClick={()=>{
                navigate("/company/companyCoupons")}}></Button>
            </Box>
            {coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)}
        </div>
    );
}

export default CompanyCouponsByMaxPrice;
