function Item(props: BoxProps) {
    const { sx, ...other } = props;
    return (
    <Box
    sx={{
    p: 1,
    m: 1,
    color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
    }}
    {...other}
/>
);
}
function AddCoupon(): JSX.Element {
    const{register,handleSubmit, formState:{errors}}= useForm<Coupon>();
    const navigate=useNavigate();
    const send=(coupon:Coupon)=>{
    console.log(coupon);
    jwtAxios.post(globals.company.addCoupon,coupon)
    .then((response)=>{
    if(response.status==200){
        coupon.id=response.data;
      
        advNotify.success(SccMsg.ADD_COUPON);
    }else{
        notify.error("something went wrong");
    }})
    .then(()=>{
        store.dispatch(addCoupon(coupon));
        navigate("/company/companyCoupons",{state:{companyId:0}});
    })
    .catch(err=>{
    console.log(err);
    })
    }
    const [category, setCategory] = useState("");
    
    // this need to be the min value for client side validition
    var startDate = new Date().toISOString().slice(0, 10);
    
        const handleChange = (event: SelectChangeEvent) => {
            setCategory(event.target.value as string);
        };
        
    
    return (
    
        <div className="addCoupon Single" style={{ textAlign: "center" }}>
        <Card sx={{ maxWidth: 800 , border: "1px solid white" }}>
        <Typography color="primary"  className="headline" variant="h5">
        Add Coupon
        </Typography>
        <hr />
        <form onSubmit={handleSubmit(send)}>
        <Box component="form"
        sx={{ display: 'flex', flexWrap: 'wrap' }}>
            <div style={{ width: '100%' }}>
            <Item >
            <FormControl fullWidth >
            <InputLabel id="demo-simple-select-label">Category</InputLabel>
            <Select  {...register("category",{
                required: "Please select a category",
            })}    
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={category}  name="category"   
            label="Category"
            onChange={handleChange}
            >
                {Category.map((item,index)=>
            <MenuItem key={index} value={item}>{item}</MenuItem>
            )}
            </Select>
            </FormControl> 
            </Item>
            </div>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.category?.message}
            </FormHelperText>
            <div style={{ width: '100%' }}>
            <Item >
            <TextField
            label="title"
            variant="outlined"
            name="title"
            className="TextBox"
            fullWidth
            {...register("title", {
            required: {
                value: true,
                message: "Please enter your title",
            },
            })}   
        ></TextField>
        </Item>
        </div>
        <div style={{ width: '100%' }}>
        <Item >
        <TextField  
            label="description"
            variant="outlined"
            name="description"
            className="TextBox"
            fullWidth
            {...register("description", {
                required: {
                value: true,
                message: "Please enter your description",
            },
            })}
        ></TextField>
        </Item>
        </div>
        <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.description?.message}
            </FormHelperText>
            <br /> 
        <div style={{ width: '100%' }}>
        <Box
        sx={{
        display: 'grid',
        columnGap: 3,
        rowGap: 1,
        gridTemplateColumns: 'repeat(2, 1fr)',
        }}
        >

        <Item >  
            <TextField
            type={"number"}
            name="amount"
            label="amount"
            className="TextBox "
            fullWidth
            // InputProps={{
            //     inputProps: { min: 1 }
            //   }}
            //   onKeyPress={(event) => {
            //     if (event?.key === '-' || event?.key === '+') {
            //       event.preventDefault();
            //     }
            //   }}
            // inputProps={{min:1}}
            // size="small"
            // sx={{ m: 1, width: '45ch' }}
            {...register("amount", {
                min:1,
                required: {
                value: true,
                message: "Please enter your amount",
                },
                validate: {
                    positiveNumber: (value) => {
                    console.log("positiveNumber check");
                    return (value>=0 );
                    }},
            
            })}
            ></TextField>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.amount?.message }
            { errors.amount?.message &&errors.amount.type == "min" && <span>amount should be positive</span> }
            </FormHelperText>
            
        </Item>
        <Item >
        <TextField
            type={"number"}
            name="price"
            label="price"
            className="TextBox "
            fullWidth
            {...register("price", {
                required: {
                value: true,
                message: "Please enter your price",
                },
                min:0
            })}
            ></TextField>
            <FormHelperText style={{ color: "#d32f2f" }}>
            {errors.price?.message}
            </FormHelperText>
        </Item>
        </Box>
        </div>
        <div style={{ width: '100%' }}>
            <Item >
            <TextField
            name="image"
            label="image"
            className="TextBox"
            fullWidth
            
            {...register("image", {
                required: {
                value: true,
                message: "Please enter your image",
                },
            })}
            > </TextField>
            </Item>
        </div>
        <div style={{ width: '100%' }}>
        <Box
        sx={{
        display: 'grid',
        columnGap: 3,
        rowGap: 1,
        gridTemplateColumns: 'repeat(2, 1fr)',
        }}
        >
            <Item >
            <TextField
            type={"date"}
            name="startDate"
            fullWidth
            label="start Date"
            className="TextBox "
            value={startDate}
            {...register("startDate", {
                required: {
                value: true,
                message: "Please enter your start date",
                },
            })}
            />
            </Item>
            <Item  >
            <TextField
            type={"date"}
            name="endDate"
            label="end date"
            fullWidth
            className="TextBox "
            {...register("endDate", {
                required: {
                value: true,
                message: "Please enter your end date",
                },
            })}
            />
            </Item>
        </Box>
    </div>
</Box>
                <ButtonGroup className="contained" fullWidth style={{justifyContent:"space-between" , display:"flex"}}>
                    <Button type="submit" variant="contained"  color="primary" startIcon={<AddIcon/>}  > Add</Button> 
                    <Button type="reset"  variant="contained" color="secondary" startIcon={<ClearIcon/>} >Reset</Button> 
                </ButtonGroup> 
        </form>
        </Card>
        </div>
    ); 
}

export default AddCoupon;
