import { Button, ButtonGroup,  Dialog,  DialogActions,  DialogContent, DialogContentText, DialogTitle, Table, TableBody, TableCell, TableHead, TableRow, Typography } from "@mui/material";
import Company from "../../../modal/Company";
import ListIcon from "@mui/icons-material/List";
import "./singleCompany.css";
import { useState, useEffect } from 'react';
import EditIcon from "@mui/icons-material/Edit";
import HighlightOffIcon from '@mui/icons-material/HighlightOff';

import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';

import Coupon from './../../../modal/Coupon';
import SingleCoupon from "../singleCoupon/singleCoupon";
import { useNavigate } from 'react-router-dom';
import React from "react";
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import advNotify, { SccMsg } from "../../../utils/notify_advenced";
import notify from './../../../utils/notify';
import { store } from './../../../redux/store';
import { deleteCompany } from "../../../redux/companyState";


interface SingleCompanyProps {
    companyData : Company ;
	
}


function SingleCompany(props: SingleCompanyProps): JSX.Element {
    const navigate=useNavigate();
     // dialog conditions
    const [open, setOpen] = useState(false);
    const remomveCompany=()=>{

        handleClose();
        console.log("remove")
        jwtAxios.delete(globals.admin.deleteCompany+props.companyData.id)
        .then((response)=>{
            if(response.status==200){
                advNotify.success(props.companyData.name+" company "+SccMsg.DELETE_COMPANY);
            }else{
                notify.error("something went wrong")
            }
        })
        .then(()=>{
            store.dispatch(deleteCompany(props.companyData.id));
        })
        .catch(err=>{
            console.log(err);
        })
    }
  
    //dialog open box 
    const handleClickOpen = () => {
    setOpen(true);
    };
    //dialog close box
    const handleClose = () => {
    setOpen(false);
    };
    
    
    return (
        <div className="singleCompany Single" style={{ textAlign: "center" }}>
        <Card sx={{ minWidth: 275 }}>
        <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
        {props.companyData.id}
        </Typography>
        <Typography variant="h5" component="div">
        {props.companyData.name}
        </Typography>
        <Typography variant="body2">
        Email - {props.companyData.email}
        <br />
        </Typography>
        </CardContent>
        <CardActions style={{textAlign:'center'}}>
        <Button style={{margin:"10px"}}
            startIcon={<ListIcon  style={{minWidth: '30px'}}/>}
            size="small"
            type="submit"
            color="info"
            variant="contained"
            onClick={()=>{
                if (store.getState().authState.userType!='ADMIN'){
                    props.companyData.id = 0;
                    console.log("company:",props.companyData.id)
                }
                else {
                    console.log("admin:",props.companyData.id)
                }
                navigate("/company/companyCoupons",{state:{companyId:props.companyData.id},});
                }} 
            >
            coupons 
            </Button>
            {(store.getState().authState.userType=='ADMIN') && <Button style={{margin:"10px"}}
            startIcon={<EditIcon style={{minWidth: '30px'}} />}
            size="small"
            type="submit"
            color="info"
            variant="contained"
            onClick={() => {
            navigate("/admin/updateCompany", {
                state: { companyId: props.companyData.id },
            });
            }}
            >
            Update 
            </Button>}
            {(store.getState().authState.userType=='ADMIN') && <Button style={{margin:"10px"}}
            startIcon={<HighlightOffIcon style={{minWidth: '30px'}}/>}
            size="small"
            type="submit"
            color="error"
            variant="contained"
            onClick={handleClickOpen}
            >
            Delete
            </Button>}
            <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
            <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to delete this company? "}
            </DialogTitle>
            <DialogContent>
            <DialogContentText id="alert-dialog-description">
            By delete this company , all customer coupons purchase history will be deleted
            </DialogContentText>
            </DialogContent>
            <DialogActions></DialogActions>
            <DialogActions>
            <Button onClick={handleClose}>Disagree</Button>
            <Button onClick={ remomveCompany} >
            Agree
            </Button>
            </DialogActions>
            </Dialog>
            </CardActions>
            
        </Card>
                
        </div>
    );
}

export default SingleCompany;
