import "./myMain.css";
import { useState } from 'react';
import Coupon from "../../../../modal/Coupon";
import { useEffect } from 'react';
import { store } from "../../../../redux/store";
import jwtAxios from "../../../../utils/JWTaxios";
import globals from './../../../../utils/global';
import SingleCoupon from "../../../company/singleCoupon/singleCoupon";
import { Typography } from "@mui/material";
import { downloadCouponsDB } from "../../../../redux/guestState";
import notify from './../../../../utils/notify';
import couponHomePic from "../../../../assests/couponsHomePic.jpg";
import Box from '@mui/material/Box';
import Fab from '@mui/material/Fab';
import AddIcon from '@mui/icons-material/Add';
import { useNavigate } from 'react-router-dom';




function MyMain(): JSX.Element {
    const [coupons,setCoupons]=useState<Coupon[]>([]);
    const navigate= useNavigate();

    useEffect(()=>{
        if(store.getState().authState.userType==='GUEST'){
            jwtAxios.get<Coupon[]>(globals.general.allCoupons)
            .then((response)=>{
                if(response.status==200){
                    store.dispatch(downloadCouponsDB(response.data));
                }else{
                    notify.error("Something went wrong");
                }
            })
            .then(()=>{
                setCoupons(store.getState().guestState.coupons)
            })
            .catch((err)=>{
                console.log(err);
            })
        }else{
            setCoupons(store.getState().guestState.coupons);
        }

    },[])
    return (
        <div className="myMain">
            <Typography variant="h4" color="primary">Welcome to Our Coupons Website</Typography>
			{(store.getState().authState.userType != 'COMPANY') && coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)}
  
      
            <br />
           
            {(store.getState().authState.userType == 'COMPANY') && <Box sx={{ '& > :not(style)': { m: 1 } }}>
            <Typography variant="h4" color="primary">Hello Company</Typography>
            <br />
            <img src={couponHomePic}/>
            <br />
              <Fab color="primary"  aria-label="add" onClick={()=>{navigate('company/addCoupon')}}>
           <AddIcon />
            </Fab>
            <Typography variant="h6" color="primary">Click to Add new Coupon</Typography>
           </Box>}

        </div>
    );
}

export default MyMain;
