import Coupon from "../modal/Coupon";

export class guestState{
    coupons:Coupon[] = [];
}
export enum guestActionType{
    DOWNLOAD_COUPONS_DB="Download Coupons DB",
    ADD_NEW_COUPON="Add New Coupon",
    UPDATE_AMOUNT_COUPON="Update Amount Coupons DB",
}
export interface guestAction{
    type: guestActionType,
    payload?:any;
}
export function downloadCouponsDB(coupons:Coupon[]):guestAction{
    return {type: guestActionType.DOWNLOAD_COUPONS_DB,payload:coupons};
}
export function addNewCoupon(coupon:Coupon):guestAction{
    return{type:guestActionType.ADD_NEW_COUPON,payload:coupon};
}
export function updateAmount(coupon:Coupon):guestAction{
    return{type: guestActionType.UPDATE_AMOUNT_COUPON,payload:coupon};
}

export function guestReducer(currentState :guestState = new guestState, action:guestAction):guestState{
    let newState={...currentState};
    switch (action.type) {
      case guestActionType.DOWNLOAD_COUPONS_DB:
        newState.coupons = action.payload;
        break;

      case guestActionType.ADD_NEW_COUPON:
        newState.coupons.push(action.payload);
        break;
        case guestActionType.UPDATE_AMOUNT_COUPON:
            var updateAllCoupons=[...newState.coupons].filter(item=>item.id!= action.payload.id);
            updateAllCoupons.push(action.payload);
            newState.coupons=updateAllCoupons;
            break;
    }
    return newState;
}