import "./companyCoupons.css";
import { useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { useNavigate } from 'react-router-dom';
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import advNotify, { ErrMsg } from "../../../utils/notify_advenced";
import SingleCoupon from "../singleCoupon/singleCoupon";
import { SyntheticEvent } from 'react';
import { Box, BoxProps, Button, Fade, FormControl, FormControlLabel, InputLabel, MenuItem, Select, SelectChangeEvent, Switch, TextField } from "@mui/material";
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import DirectionsIcon from '@mui/icons-material/Directions';
import { width } from "@mui/system";
import { type } from 'os';
import category from "../../../modal/Category";
import { useLocation } from 'react-router-dom';
import Company from "../../../modal/Company";
import notify from './../../../utils/notify';
import { CouponState } from './../../../redux/couponState';



function Item(props: BoxProps) {
        const { sx, ...other } = props;
        return (
        <Box
        sx={{
        p: 1,
        m: 1,
        bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : 'grey.100'),
        color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
        border: '1px solid',
        borderColor: (theme) =>
        theme.palette.mode === 'dark' ? 'grey.800' : 'grey.300',
        borderRadius: 2,
        fontSize: '0.875rem',
        fontWeight: '700',
        ...sx,
        }}
        {...other}
    />
    );
}
function CompanyCoupons(): JSX.Element {
    const location=useLocation();
    let{companyId}=location.state as any  
    const[display,setDisplay]=useState(false)
    const[coupons,setCoupons]=useState<Coupon[]>([])
    const navigate=useNavigate();
    const[foundCategory,setCategory]=useState("");
    const[foundPrice,setPrice]=useState(null);
    const search=(sender:SelectChangeEvent)=>{
        let value= (sender.target as HTMLSelectElement).value;
        setCategory(value);
    }

    useEffect(()=>{
        if(store.getState().authState.userType==='ADMIN'){
        let company =store.getState().companyState.companies.find(item=>item.id==companyId);
        setCoupons(company.coupons);
        }else if(store.getState().authState.userType=== "COMPANY"){
        setCoupons(store.getState().CouponState.coupons);
        }else{
        advNotify.error(ErrMsg.LOGIN_FIRST);
        navigate("/login");
        }
    },[])
    
    const searchPrice=(sender: SyntheticEvent)=>{
        const priceValue = (sender.target as HTMLInputElement).value;
        setPrice(priceValue);
    }
    const [checked, setChecked] = useState(false);

    const handleChange = () => {
    setChecked((prev) => !prev);
    };
    return (
        <div className="companyCoupons">
        <FormControlLabel
        control={<Switch checked={checked} onChange={handleChange} />}
        label="filters"
        />
        <Fade in={checked}>
        <div style={{ width: '50%' }}>
        <Box 
        sx={{ display: 'flex', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}
        >   
        <Item sx={{ flexGrow: 1 }}>
        <FormControl fullWidth >
                <InputLabel id="demo-simple-select-label">Search By Category </InputLabel>
                <Select 
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                name="category"   
                label="Category"
                value={foundCategory}
                onChange={search}
            >
            {category.map((item,index)=>
            <MenuItem key={index} value={item}>{item}</MenuItem>
            )}
            </Select>
            </FormControl> 
        </Item>
        <Item sx={{ flexGrow: 1 }}>  
        <FormControl fullWidth >
        <InputLabel id="demo-simple-select-label">Search By Price </InputLabel>
        <TextField type={"number"} onChange={searchPrice} value={foundPrice} variant="outlined" className="TextBox"> </TextField>
        </FormControl>
        </Item>
        <Box sx={{display: 'flex', p: 1,m:1 }}>
        <Button fullWidth variant="contained" color="info" onClick={() => {
                setDisplay(true); 
        }}>Find</Button> 
        </Box>
        <Box sx={{display: 'flex', p: 1,m:1 }}>
        <Button fullWidth variant="contained"  color="secondary" onClick={() => {
            setDisplay(false); 
            setCategory("");
            setPrice(null);
        }}>Reset</Button> 
        </Box>
        </Box>
    </div> 
    </Fade> 
            {( display==true) &&coupons.filter(item=>item.category==foundCategory).map(item=><SingleCoupon key={item.id} couponData={item}/>)}
            {( display==true) &&coupons.filter(item=>item.price <=foundPrice).map(item=><SingleCoupon key={item.id} couponData={item}/>)}  
            {( display==!true) &&coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)} 
        </div>
        
    );
}

export default CompanyCoupons;

