import "./companyCouponsByCategory.css";
import { useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import { useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { store } from './../../../redux/store';
import SingleCoupon from "../singleCoupon/singleCoupon";
import { Box, Button, Typography } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { useNavigate } from 'react-router-dom';
import Company from "../../../modal/Company";

function CompanyCouponsByCategory(): JSX.Element {
    const location = useLocation();
    const{companyIdByCat}=location.state as any;
    const {categoryName}= location.state as any
    const[coupons,setCoupons]=useState<Coupon[]>([])
    const[company,setCompany]=useState(new Company());

    const navigate=useNavigate();
    useEffect(()=>{
        if(store.getState().authState.userType=='COMPANY'){

       
        setCoupons(
      store.getState().CouponState.coupons.filter(item=>item.category === categoryName));
        }
        if(store.getState().authState.userType=='ADMIN'){
        //   setCompany( store.getState().companyState.companies.find(item=>item.id==companyIdByCat));
        }
    },[])
    console.log(coupons);
    return (
        <div className="companyCouponsByCategory">
             <Box sx={{ p: '2px 4px'  }}>
            <Typography variant="h4" color={"secondary"} >All Coupons By {categoryName} category</Typography>
            <Button  variant="contained" color="info" startIcon={<ArrowBackIcon/>} onClick={()=>{
                navigate("/company/companyCoupons")}}></Button>
            </Box>
            {/* {(store.getState().authState.userType=='ADMIN') && company.coupons?.filter(item=>item.category==categoryName).map(item=><SingleCoupon key={item.id} couponData={item}/>)} */}
			{coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)}
        </div>
    );
}

export default CompanyCouponsByCategory;
