import "./customerCouponsByCategory.css";
import { useLocation, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { useEffect } from 'react';
import { store } from "../../../redux/store";
import { Box, Button, Typography } from "@mui/material";
import SingleCoupon from "../../company/singleCoupon/singleCoupon";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
function CustomerCouponsByCategory(): JSX.Element {
    const location=useLocation();
    const{categoryName}=location.state as any;
    const[coupons,setCoupons]=useState<Coupon[]>([]);
    const navigate=useNavigate();
    useEffect(()=>{
        setCoupons(store.getState().customerState.customers[0].coupons?.filter(item=>item.category==categoryName))
    },[])
    
    return (
        <div className="customerCouponsByCategory">
			<Box sx={{ p: '2px 4px'  }}>
            <Typography variant="h4" color={"secondary"} >All Coupons By {categoryName} category</Typography>
            <Button  variant="contained" color="info" startIcon={<ArrowBackIcon/>} onClick={()=>{
                navigate("/customer/customerCoupons")}}></Button>
            </Box>
			{coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)}
        </div>
    );
}

export default CustomerCouponsByCategory;
