import { Message } from "@mui/icons-material";
import { Button, ButtonGroup, TextField, Typography } from "@mui/material";
import { useForm } from "react-hook-form";
import Customer from "../../../modal/Customer";
import "./addCustomer.css";
import ClearIcon from '@mui/icons-material/Clear';
import AddIcon from '@mui/icons-material/Add';
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import advNotify, { SccMsg } from "../../../utils/notify_advenced";
import notify from "../../../utils/notify";
import { store } from "../../../redux/store";
import { addCustomer } from "../../../redux/customerState";
import { useNavigate } from 'react-router-dom';


function AddCustomer(): JSX.Element {
    const navigate=useNavigate();
    const {register,handleSubmit, formState:{errors}}= useForm<Customer>();
    const send=(customer:Customer)=>{
        console.log(customer);
        jwtAxios.post(globals.admin.addCustomer,customer)
        .then((response)=>{
            if(response.status==200){
                customer.id=response.data;
                advNotify.success(SccMsg.ADD_CUSTOMER);
            }else{
                notify.error("Something went wrong");
            }
        })
        .then(()=>{
            store.dispatch(addCustomer(customer));
            navigate("/admin/getAllCustomers");
        })
        .catch((err)=>{
            console.log(err);
            advNotify.error(err);
        });
    }

    return (
        <div className="addCustomer BoxStyle">
            <Typography className="headline" variant="h3">Add Customer</Typography> 
            <hr />
            <form onSubmit={handleSubmit(send)}>
                <TextField label="firast Name" name="firstName" 
                variant="outlined" className="TextBox" fullWidth
                {...register("firstName",{
                    required:{
                    value:true,
                    message: "Please enter your first name"
                }})}>
                </TextField>
                {errors.firstName?.message}
                <br /> <br />
                <TextField label="lastName" name="lastName"
                variant="outlined" className="TextBox" fullWidth
                {...register("lastName",{
                    required:{
                    value:true,
                    message: "Please enter your last name",
                }})}>
                </TextField>
                {errors.lastName?.message}
                <br /> <br />
                <TextField label="email" name="email"
                variant="outlined" className="TextBox" fullWidth
                {...register("email",{
                    required:{
                    value:true,
                    message: "Please enter your email",
                },
                pattern: {
                    value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                    message: "invalid email address"
                }
                })}>
                </TextField>
                {errors.email?.message}
                <br /> <br />
                <TextField  label="password" name="password" type={"password"}
                variant="outlined" className="TextBox" fullWidth
                {...register("password",{
                    required:{
                    value:true,
                    message: "Please enter your password",
                    }})}>
                </TextField>
                {errors.password?.message}
                <br /> <br />
                <ButtonGroup variant="contained" fullWidth>
                <Button style={{margin:"10px"}} type="submit" color="primary" startIcon={<AddIcon style={{minWidth: '30px'}}/>}  > Add</Button> 
                    <Button style={{margin:"10px"}} type="reset" color="secondary" startIcon={<ClearIcon style={{minWidth: '30px'}}/>} >Reset</Button> 
                </ButtonGroup>



            </form>
		
        </div>
    );
}

export default AddCustomer;
