import { Button, ButtonGroup, Card, CardActions, CardContent, FormControl, FormControlLabel, FormHelperText, FormLabel, RadioGroup, TextField, Typography } from "@mui/material";
import "./updateCompany.css";
import Company from './../../../modal/Company';
import { useForm } from "react-hook-form";
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import notify from "../../../utils/notify";
import advNotify, { ErrMsg, SccMsg } from "../../../utils/notify_advenced";
import { useLocation, useNavigate } from "react-router-dom";
import { SyntheticEvent, useEffect, useState } from "react";
import { store } from "../../../redux/store";
import { useDispatch } from 'react-redux';
import { updateToken } from "../../../redux/authState";
import { updateCompany } from "../../../redux/companyState";



function UpdateCompany(): JSX.Element {
   /* we will use useLocation to get the paramters that we are passing */
    const location = useLocation();
    const navigate=useNavigate();
    const {companyId} = location.state as any
    const [company,setCompany] = useState(new Company());


    const {register, handleSubmit, formState: { errors } } = useForm<Company>();

  

        const send = () => {
          console.log(company);
          jwtAxios.put(globals.admin.updateCompany,company)
          .then(response=>{
          if(response.status == 200){
            advNotify.success(SccMsg.UPDATE_COMPANY);
          }else{
            notify.error("something went wrong");
            console.log(response.data);
          }
          }).then(()=>{
            store.dispatch(updateCompany(company));
            navigate("/admin/getAllCompanies");
          })
          .catch(err=>{
            advNotify.error(err)
          })
        };


  useEffect(() => {
    console.log(companyId);
      setCompany(
      store.getState().companyState.companies.find((item) => item.id == companyId));
  }, []);


  const handleInputChange = (args:any) => {
    //const name = e.target.name 
    //const value = e.target.value 
    const { name, value } = args.target ;
    setCompany({
      ...company,
      [name]: value,
    });
  };



    return (
      <div className="updateCompany BoxStyle">
        <Typography className="headLine" variant="h3">
          Update Company 
        </Typography>
        <hr />
        <form onSubmit={handleSubmit(send)}>
          <TextField
            value={company.name}
            
            label="company name "
            variant="outlined"
            name="name"
            className="TextBox"
            fullWidth
            {...register("name")}
            disabled
          ></TextField>
          <br />
          <br />
          <TextField
            label="company email"
            name="email"
            variant="outlined"
            className="TextBox"
            fullWidth
            {...register("email", {
              required: {
                value: true,
                message: "Please enter your email",
              },
            })}
            value={company.email}
            onChange={handleInputChange}
          ></TextField>
          {errors.email?.message}
          <br /> <br />
          <br /> <br />
          <ButtonGroup variant="contained" fullWidth>
            <Button style={{margin:"10px"}} type="submit" color="primary" >
              Send
            </Button>
            <Button style={{margin:"10px"}} type="reset" color="secondary">
              reset
            </Button>
          </ButtonGroup>
        </form>
        <Button onClick={()=>{navigate("/admin/getAllCompanies")}} variant="contained" color="success">All Companies</Button>
      </div>
    );
}

export default UpdateCompany;
