import "./customerCoupons.css";
import {  SyntheticEvent, useEffect,  useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { store } from "../../../redux/store";
import { Box, BoxProps, Button, Fade, FormControl, FormControlLabel, InputLabel, MenuItem, Select, SelectChangeEvent, Switch, TextField, Typography } from "@mui/material";
import category from "../../../modal/Category";
import { useLocation, useNavigate } from 'react-router-dom';
import SingleCoupon from "../../company/singleCoupon/singleCoupon";
import Customer from "../../../modal/Customer";
import Slider from 'rc-slider';
import Range from 'rc-slider';
import 'rc-slider/assets/index.css';

function Item(props: BoxProps) {
    const { sx, ...other } = props;
    return (
    <Box
    sx={{
    p: 1,
    m: 1,
    bgcolor: (theme) => (theme.palette.mode === 'dark' ? '#101010' : 'grey.100'),
    color: (theme) => (theme.palette.mode === 'dark' ? 'grey.300' : 'grey.800'),
    border: '1px solid',
    borderColor: (theme) =>
    theme.palette.mode === 'dark' ? 'grey.800' : 'grey.300',
    borderRadius: 2,
    fontSize: '0.875rem',
    fontWeight: '700',
    ...sx,
    }}
    {...other}
/>
);
}

function CustomerCoupons(): JSX.Element {
    const [coupons,setCoupons]= useState<Coupon[]>([]);
    const navigate=useNavigate();
    const location=useLocation();
    const[display,setDisplay]=useState(false)
    let{customerId}=location.state as any  
    const[foundCategory,setCategory]=useState("");
    const[foundPrice,setPrice]=useState(null);
    const search=(sender:SelectChangeEvent)=>{
        let value= (sender.target as HTMLSelectElement).value;
        setCategory(value);
        console.log(foundCategory);
    }
    const [checked, setChecked] = useState(false);

    const handleChange = () => {
    setChecked((prev) => !prev);
    };
    function valuetext(value: number) {
        return `${value}C`;
    }
   
    const searchPrice=(sender: SyntheticEvent)=>{
        const priceValue = (sender.target as HTMLInputElement).value;
        setPrice(priceValue);
    }
    const rangeSelector = (event:any, newValue:any) => {
    setPrice(newValue);
    };
    useEffect(()=>{
        console.log(customerId);
        if(store.getState().authState.userType=='ADMIN') {
        let customer=store.getState().customerState.customers.find(item=>item.id==customerId);
        setCoupons(customer.coupons);
        } else if(store.getState().authState.userType=='CUSTOMER'){
            let customer=store.getState().customerState.customers[0];
            setCoupons(customer.coupons);
        }
    },[])


    return (
        <div className="customerCoupons">
            <Typography variant="h5" gutterBottom component="div"  color={"primary"} >My Coupons </Typography>
            <FormControlLabel
        control={<Switch checked={checked} onChange={handleChange} />}
        label="filters"
        />
        <Fade in={checked}>
        <div style={{ width: '50%' }}>
        <Box 
        sx={{ display: 'flex', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}
        >   
        <Item sx={{ flexGrow: 1 }}>
        <FormControl fullWidth >
                <InputLabel id="demo-simple-select-label">Search By Category </InputLabel>
                <Select 
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                name="category"   
                label="Category"
                value={foundCategory}
                onChange={search}
            >
            {category.map((item,index)=>
            <MenuItem key={index} value={item}>{item}</MenuItem>
            )}
            </Select>
            </FormControl> 
        </Item>
        <Item sx={{ flexGrow: 1 }}>  
        <FormControl fullWidth >
        <InputLabel id="demo-simple-select-label">Search By Price </InputLabel>
        <TextField type={"number"} onChange={searchPrice} value={foundPrice} variant="outlined" className="TextBox"> </TextField>
        </FormControl>
        </Item>
        <Box sx={{display: 'flex', p: 1,m:1 }}>
        <Button fullWidth variant="contained" color="info" onClick={() => {
                setDisplay(true); 
        }}>Find</Button> 
        </Box>
        <Box sx={{display: 'flex', p: 1,m:1 }}>
        <Button fullWidth variant="contained"  color="secondary" onClick={() => {
            setDisplay(false); 
            setCategory("");
            setPrice(null);
        }}>Rest</Button> 
        </Box>
        </Box>
    </div> 
    </Fade> 

            {( display==true) &&coupons.filter(item=>item.category==foundCategory).map(item=><SingleCoupon key={item.id} couponData={item}/>)}
            {( display==true) &&coupons.filter(item=>item.price <=foundPrice).map(item=><SingleCoupon key={item.id} couponData={item}/>)}  
			{( display!=true) &&coupons.map(item=><SingleCoupon key={item.id} couponData={item}/> )}
          
        
        </div>
    );
}

export default CustomerCoupons;
