import "./customerCouponsByMaxPrice.css";
import { useLocation, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import Coupon from "../../../modal/Coupon";
import { useEffect } from 'react';
import { store } from './../../../redux/store';
import { Box, Button, Typography } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import SingleCoupon from "../../company/singleCoupon/singleCoupon";
import Customer from "../../../modal/Customer";

function CustomerCouponsByMaxPrice(): JSX.Element {
    const location=useLocation();
    const navigate=useNavigate();
    const[customer,setCustomer]=useState(new Customer());

    const {maxPrice}=location.state as any;
    const[coupons,setCoupons]=useState<Coupon[]>([]);
  useEffect(()=>{
      //setCoupons(store.getState().CouponState.coupons.filter(item=>item.price <=maxPrice))
      //setCustomer(store.getState().customerState.customers.find(item=>item.id));
      setCoupons(store.getState().customerState.customers[0].coupons?.filter(item=>item.price<=maxPrice));
      
  },[])
 


    return (
        <div className="customerCouponsByMaxPrice">
			<Box sx={{ p: '2px 4px'  }}>
            <Typography variant="h4" color={"secondary"} >All Coupons up to {maxPrice}$ price</Typography>
            <Button  variant="contained" color="info" startIcon={<ArrowBackIcon/>} onClick={()=>{
            navigate("/customer/customerCoupons")}}></Button>
            </Box>
            {/* {customer.coupons?.filter(i=>i.price<=maxPrice).map(item=><SingleCoupon key={item.id} couponData={item}/>)} */}
            {coupons.map(item=><SingleCoupon key={item.id} couponData={item}/>)}
        </div>
    );
}

export default CustomerCouponsByMaxPrice;
