import "./customerDetails.css";
import { useState, useEffect } from 'react';
import Customer from "../../../modal/Customer";
import { store } from './../../../redux/store';
import SingleCustomer from "../singleCustomer/singleCustomer";

function CustomerDetails(): JSX.Element {
    const[customer,setCustomer]=useState<Customer>(new Customer());
    useEffect(()=>{
        setCustomer(store.getState().customerState.customers.find(item=>item.id))
    },[])
 
    return (
        <div className="customerDetails">
          
            {<SingleCustomer key={customer.id} customerData={customer}/>}

	
        </div>
    );
}

export default CustomerDetails;
