import Customer from "../../../modal/Customer";
import "./singleCustomer.css";
import { Button, ButtonGroup,  Card,  CardActions,  CardContent,  Dialog,  DialogActions,  DialogContent, DialogContentText, DialogTitle, Table, TableBody, TableCell, TableHead, TableRow, Typography } from "@mui/material";
import { useState } from 'react';
import EditIcon from "@mui/icons-material/Edit";
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import ListIcon from "@mui/icons-material/List";
import { useNavigate } from 'react-router-dom';
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import advNotify, { ErrMsg, SccMsg } from "../../../utils/notify_advenced";
import { store } from './../../../redux/store';
import { deleteCustomer } from "../../../redux/customerState";
import { useEffect } from 'react';

interface SingleCustomerProps {
	customerData:Customer
}

function SingleCustomer(props: SingleCustomerProps): JSX.Element {
    const[display,setDisplay]=useState(true);


    const removeCustomer=()=>{
        handleClose();
        jwtAxios.delete(globals.admin.deleteCustomer+props.customerData.id)
        .then(response=>{
            if(response.status==200){
                advNotify.success(props.customerData.firstName+" "+props.customerData.lastName+" "+SccMsg.DELETE_CUSTOMER)
            }else{
                advNotify.error(ErrMsg.DELETE_CUSTOMER)
            }
        })
        .then(()=>{
            store.dispatch(deleteCustomer(props.customerData.id));
        })
        .catch(err=>{
            console.log(err);
            advNotify.error(err);
        })
    }
    const navigate= useNavigate();
    const [open, setOpen] = useState(false);
    const handleClickOpen = () => {
    setOpen(true);
    }; 
    const handleClose = () => {
    setOpen(false);
    };
    return (
        <div className="singleCustomer Single" style={{ textAlign: "center" }}>
        <Card sx={{ minWidth: 275 }}>
        <CardContent> 
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom> {props.customerData.id} </Typography>
        <Typography variant="h5" component="div">
        First Name: {props.customerData.firstName} <br />
        Last Name: {props.customerData.lastName}
        </Typography>
        <Typography variant="body2">  
        Email: {props.customerData.email}
        <br />
        </Typography>
        </CardContent>
        <CardActions style={{textAlign:'center'}}>
            
        <ButtonGroup>
            <Button style={{margin:"10px"}}
            startIcon={<ListIcon style={{minWidth: '30px'}} />}
            size="small"
            type="submit"
            color="info"
            variant="contained"
            onClick={() => {
            navigate("/customer/customerCoupons",{state:{customerId:props.customerData.id},});
            }}
            >
            coupons
            </Button>{" "}
            { (store.getState().authState.userType=='ADMIN')&& <Button style={{margin:"10px"}}
            startIcon={<EditIcon style={{minWidth: '30px'}}  />}
            size="small"
            type="submit"
            color="info"
            variant="contained"
            onClick={() => {
            navigate("/admin/updateCustomer", {
                state: { customerId: props.customerData.id },
            });
            }}
            >
            Update
            </Button>}
            {(store.getState().authState.userType=='ADMIN')&& <Button style={{margin:"10px"}}
            startIcon={<HighlightOffIcon style={{minWidth: '30px'}} />}
            size="small"
            type="submit"
            color="error"
            variant="contained"
            onClick={handleClickOpen}
            >
            delete 
            </Button> }
            <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
            <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to delete this customer ?"}
            </DialogTitle>
            <DialogContent>
            <DialogContentText id="alert-dialog-description">
            Delete Customer from System 
            </DialogContentText>
            </DialogContent>
            <DialogActions></DialogActions>
            <DialogActions>
            <Button onClick={handleClose}>Disagree</Button>
            <Button onClick={ removeCustomer} >
                Agree
            </Button>
            </DialogActions>
            </Dialog>
        </ButtonGroup>
        </CardActions>
    </Card>
        </div>
    );
}

export default SingleCustomer;
