import {  Button, Table, TableHead, TextField, Typography } from "@mui/material";
import "./getAllCompanies.css";
import Company from './../../../modal/Company';
import { SyntheticEvent, useEffect, useState } from "react";
import axios from "axios";
import SingleCompany from "../../company/singleCompany/singleCompany";
import { TableBody, TableCell, TableRow,Alert } from "@mui/material";
import { color } from "@mui/system";
import globals from './../../../utils/global';

import advNotify, { SccMsg } from "../../../utils/notify_advenced";
import { useNavigate } from 'react-router-dom';
import jwtAxios from "../../../utils/JWTaxios";
import { updateToken } from "../../../redux/authState";
import { useDispatch } from "react-redux";
import { store } from './../../../redux/store';
import { downloadCompanies } from "../../../redux/companyState";
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import DirectionsIcon from '@mui/icons-material/Directions';


function GetAllCompanies(): JSX.Element {
  
  let [companies,setData] = useState<Company[]>([]);
  const navigate=useNavigate();

  useEffect(() => {
    if (store.getState().authState.userType != "ADMIN") {
      advNotify.error("Please login..");
      navigate("/login");
    }
    console.log("get companies");
    setData(store.getState().companyState.companies);
    }, []);


  var [foundCompany,setFind]= useState("")
    const search=(sender: SyntheticEvent)=>{
    const value = (sender.target as HTMLInputElement).value;
    setFind(value);
  }
  var found= ()=>{
    setFind(foundCompany);
  }


  
      
    
    return (
        <div className="getAllCompanies">
      <div style={{textAlign: "center"}}>
      <Typography variant="h5" gutterBottom component="div"  color={"primary"} >Companies in The System</Typography>
      <Paper 
      component="form"
      sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: 400 ,justifyContent: 'center'}}
      >
      <IconButton sx={{ p: '10px' }} aria-label="menu">
      <MenuIcon />
      </IconButton>
      <InputBase
      onChange={search} value={foundCompany}
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search Company"
        inputProps={{ 'aria-label': 'search' }}
      />
      <IconButton type="button" onClick={found} value={foundCompany} sx={{ p: '10px' }} aria-label="search">
      <SearchIcon />
      </IconButton>
      <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
      </Paper>
      </div>
      <hr /> 
      {companies.filter(item=>item.name.match(foundCompany)).map(item=> <SingleCompany key={item.id} companyData={item}/>)}
        </div>
    );
}
      

export default GetAllCompanies;
