import { Alert, Button, ButtonGroup, TextField, Typography } from "@mui/material";

import ClearIcon from '@mui/icons-material/Clear';
import AddIcon from '@mui/icons-material/Add';
import "./addCompany.css";
import Company from './../../../modal/Company';
import { useForm } from "react-hook-form";

import globals from "../../../utils/global";
import notify from "../../../utils/notify";
import advNotify, { ErrMsg, SccMsg } from "../../../utils/notify_advenced";
import jwtAxios from "../../../utils/JWTaxios";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";

import { addCompany, downloadCompanies } from "../../../redux/companyState";
import { store } from './../../../redux/store';

function AddCompany(): JSX.Element {
    const{register,handleSubmit, formState:{errors}}= useForm<Company>();
    const navigate= useNavigate();

    const add = (newCompany:Company)=>{ 
        jwtAxios.post<Company>(globals.admin.addCompany,newCompany)
        .then(response=>{
            if(response.status==200){
            newCompany.id=response.data;
            notify.success(SccMsg.ADD_COMPANY);
            }else{
                notify.error("Something went wrong") 
            }}).then(()=>{
            store.dispatch(addCompany(newCompany))
            navigate("/admin/getAllCompanies");
            })
            .catch(err=>{
            console.log(err);
            advNotify.error(err);
            })
    }

    return (
        <div className="addCompany BoxStyle">
			<Typography className="headline" variant="h3" >Add Company</Typography><hr />
            <form onSubmit={handleSubmit(add)}>
                <TextField label="name" name="name"  variant="outlined" className="TextBox" fullWidth {...register("name",{
                    required:{
                        value: true,
                        message:"please enter a company name"
                    }
                })}>
                </TextField>
                {errors.name?.message}
                <br /><br />
                <TextField label="email" variant="outlined" name="email" className="TextBox" fullWidth {...register("email",{
                    required:{
                        value:true,
                        message:"please enter a company email"
                    },
                    pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                        message: "invalid email address"
                    }
                })}>
                </TextField>
                {errors.email?.message}
                <br /> <br />
                <TextField label="password" name="password" variant="outlined" className="TextBox" fullWidth {...register("password",{
                    required:{
                        value:true,
                        message:"please enter a password"
                    }
                })}>
                </TextField>
                {errors.password?.message}
                    <br /><br/>
                <ButtonGroup variant="contained" fullWidth >
                    <Button style={{margin:"10px"}} type="submit" color="primary" startIcon={<AddIcon style={{minWidth: '30px'}}/>}  > Add</Button> 
                    <Button style={{margin:"10px"}} type="reset" color="secondary" startIcon={<ClearIcon style={{minWidth: '30px'}}/>} >Reset</Button> 
                </ButtonGroup>
            </form>
        </div>
    );
    }

export default AddCompany;
