import { Cancel, Send } from "@mui/icons-material";
import { Button, ButtonGroup, FormControl, FormControlLabel, FormHelperText, FormLabel, Radio, RadioGroup, TextField, Typography } from "@mui/material";
import "./login.css";

import { useForm } from "react-hook-form";


import UserDetails from "../../../modal/userDetails";
import jwtAxios from "../../../utils/JWTaxios";
import notify from "../../../utils/notify";
import {ErrMsg, SccMsg} from "../../../utils/notify_advenced"
import advNotify from './../../../utils/notify_advenced';
import globals from '../../../utils/global';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from "react-redux";
import { userLogin } from "../../../redux/authState";
import { store } from './../../../redux/store';
import { downloadCompanies } from "../../../redux/companyState";
import Company from "../../../modal/Company";
import { downloadcustomers } from "../../../redux/customerState";
import Customer from "../../../modal/Customer";
import Coupon from './../../../modal/Coupon';
import { addCoupon, companyDetails, downloadCoupons } from "../../../redux/couponState";
import { addCompany } from './../../../redux/companyState';
import { addCustomer } from './../../../redux/customerState';
import AddCoupon from "../../company/addCoupon/addCoupon";



function Login(): JSX.Element {
  const {
    register,
    handleSubmit,
    formState: { errors }, } = useForm<UserDetails>();
  const navigate= useNavigate();
  const dispatch = useDispatch();

  const send = (message: UserDetails) => {
    jwtAxios.post(globals.general.login, message)
      .then((response) => {
        notify.success(SccMsg.LOGIN_APPROVED);
        dispatch(userLogin(response.headers.authorization));
        switch (store.getState().authState.userType) {
          case "ADMIN":
          console.log("case admin")
            if(store.getState().companyState.companies.length<1){
              jwtAxios.get<Company[]>(globals.admin.getAllCompanies)
              .then(response=>{
                console.log(response);
                store.dispatch(downloadCompanies(response.data));
              })
              .catch(err=>{
                advNotify.error("system have an error :(")
                console.error(err);
              })
            }
            if(store.getState().customerState.customers.length<1){
              jwtAxios.get<Customer[]>(globals.admin.getAllCustomers)
              .then(response=>{
                console.log(response);
                store.dispatch(downloadcustomers(response.data));
              })
              .catch(err=>{
                advNotify.error("system have an error :(")
                console.error(err);
              })
            }
        
            break;
          case "COMPANY":
            console.log("case company");
            if(store.getState().companyState.companies.length<1){
              jwtAxios.get<Company>(globals.company.getCompanyDetails)
              
              .then((response)=>{
              
                  let companies:Company[] = [];
                  companies.push(response.data);
                  store.dispatch(downloadCompanies(companies));
                  store.dispatch(downloadCoupons((response.data as Company).coupons));
              })
              .catch((err)=>{
              advNotify.error("system have an error :(")
              console.error(err);
              })
              }
            break;

            case 'CUSTOMER':
            if(store.getState().customerState.customers.length<1){
              jwtAxios.get<Customer>(globals.customer.getCustomerDetails)
              .then((response)=>{
                console.log(response);
                let customers:Customer[]=[];
                customers.push(response.data);
                store.dispatch(downloadcustomers(customers))
                  console.log(store.getState().customerState.customers);
                  console.log(store.getState().CouponState.coupons)
                })
                .catch((err)=>{
                  advNotify.error("system have an error :(")
                  console.error(err);
                })
              }
              break;
        }
        navigate("/");
      })
      .catch((err) => {
        advNotify.error(err);
      });
  };

  return (
    <div className="login BoxStyle" style={{ textAlign: "center" }}>
      <Typography className="headLine" variant="h4">
        Login
      </Typography>
      <hr />
      <form onSubmit={handleSubmit(send)}>
        <TextField 
          name="email"
          label="email"
          variant="outlined"
          className="TextBox"
          fullWidth
          {...register("email", {
            required: {
              value: true,
              message: "Please enter your email",
            },
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: "invalid email address"
            }
          })}
        ></TextField>
        {errors.email?.message}
        <br />
        <br />
        <TextField
        name="password"
        type={"password"}
          label="password"
          variant="outlined"
          className="TextBox"
          fullWidth
          {...register("password", {
            required: {
              value: true,
              message: "Please enter your password",
            },
          })}
        ></TextField>
        {errors.password?.message}
        <br /> <br />
        <br /> <br />
        <FormControl>
          <FormLabel id="demo-row-radio-buttons-group-label">
            Client Type
          </FormLabel>
          <RadioGroup
            row
            aria-labelledby="demo-row-radio-buttons-group-label"
            name="row-radio-buttons-group"
          >
            <FormControlLabel
              value="ADMIN"
              name="clientType"
              control={<Radio />}
              label="admin"
              {...register("clientType", {
                required: "Please select a client type",
              })}
            />

            <FormControlLabel
              value="COMPANY"
              name="clientType"
              control={<Radio />}
              label="company"
              {...register("clientType", {
                required: "Please select a client type",
              })}
            />
            <FormControlLabel
              value="CUSTOMER"
              name="clientType"
              control={<Radio />}
              label="customer"
              {...register("clientType", {
                required: "Please select a client type",
              })}
            />
          </RadioGroup>
        </FormControl>
        <FormHelperText style={{ color: "#d32f2f" }}>
          {errors.clientType?.message}
        </FormHelperText>
        <br /> <br />
        <ButtonGroup variant="contained" fullWidth>
          <Button type="submit" color="primary" >
            Send
          </Button>
          <Button type="reset" color="secondary" >
            reset
          </Button>
        </ButtonGroup>
      </form>
    </div>
  );
}

export default Login;
