import { type } from "os";
import Coupon from "../modal/Coupon";
import Customer from "../modal/Customer";

export class CustomerState{
    customers:Customer[]=[]
}

export enum CustomerActionType{
    DOWNLOAD_CUSTOMERS="Download customers",
    ADD_CUSTOMER="Add customer",
    UPDATE_CUSTOMER="Update customer",
    DELETE_CUSTOMER="Delete customer",
    PURCHASE_COUPON="Purchase coupon",
}

export interface customerAction{
type:CustomerActionType,
payload?:any;
}
export function downloadcustomers(customers:Customer[]):customerAction{
    return{type:CustomerActionType.DOWNLOAD_CUSTOMERS,payload:customers};
}
export function addCustomer(customer:Customer):customerAction{
    return{type:CustomerActionType.ADD_CUSTOMER,payload:customer};
}
export function updateCustomer(customer:Customer):customerAction{
    return {type: CustomerActionType.UPDATE_CUSTOMER, payload: customer}
}
export function deleteCustomer(customerId:number):customerAction{
    return{type:CustomerActionType.DELETE_CUSTOMER,payload:customerId};
}
export function purchaseCoupon(coupon:Coupon):customerAction{
    return{type:CustomerActionType.PURCHASE_COUPON,payload:coupon};
    
}

export function customerReducer(currenState : CustomerState = new CustomerState, action:customerAction):CustomerState{
    const newState={...currenState};
    switch (action.type) {
        case CustomerActionType.DOWNLOAD_CUSTOMERS:
        newState.customers=action.payload;
        break;
        case CustomerActionType.ADD_CUSTOMER:
            const newCustomers=[...newState.customers];
            newCustomers.push(action.payload);
            newState.customers = newCustomers;
        break;
        case CustomerActionType.DELETE_CUSTOMER:
        newState.customers=newState.customers.filter(item=>item.id != action.payload);
        break;
        case CustomerActionType.UPDATE_CUSTOMER:
        var updateCus=[...newState.customers].filter(item=>item.id!= action.payload.id);
        updateCus.push(action.payload);
        newState.customers=updateCus;     
        break;
        case CustomerActionType.PURCHASE_COUPON:
        //    const newCustomersCoupons=[...newState.customers[0].coupons];
        //    newCustomersCoupons.push(action.payload);
        //    newState.customers[0].coupons=newCustomersCoupons;
        newState.customers[0].coupons?.push(action.payload);
        console.log(newState.customers);
        break;
    }



    return newState;
}
