
import jwt_decode  from 'jwt-decode';
// data related to all components
export class authState{
    user: string="";
    userType:string="GUEST";
    userToken:string="";
    isLoggedIn:boolean = false;
}

export enum authActionType{
    USER_LOGIN="User login",
    USER_LOGOUT="User logout",
    UPDATE_TOKEN="Update token",
}

export interface authAction{
    type:authActionType,
    payload?: any;
}

export function userLogin(userToken: string):authAction{
    return{type:authActionType.USER_LOGIN , payload:userToken};
}

export function userLogout():authAction{
    return{type:authActionType.USER_LOGOUT}
}
export function updateToken(userToken:string):authAction{
    return{type:authActionType.UPDATE_TOKEN,payload:userToken};
}

//reducer 
export function authReducer(currentState: authState = new authState, action:authAction):authState{
    const newState = {...currentState};
    switch (action.type) {
        // for user login we use token parser 
        case authActionType.USER_LOGIN:
        var myToken=action.payload.replace("Bearer ", " ");
        var decodedToken= JSON.parse(JSON.stringify(jwt_decode(myToken)));
        newState.user=decodedToken.sub;
        newState.userType=decodedToken.clientType;
        newState.userToken=action.payload;
        newState.isLoggedIn=true;
        break;

        case authActionType.USER_LOGOUT:
        newState.userToken="";
        newState.userType="GUEST";
        newState.isLoggedIn=false;
        break;

        case authActionType.UPDATE_TOKEN:
        newState.userToken=action.payload;
        break;
    }


    return newState;
}
