import "./getAllCustomers.css";
import { useState, useEffect, SyntheticEvent } from 'react';
import Customer from "../../../modal/Customer";
import { store } from './../../../redux/store';
import advNotify, { ErrMsg } from "../../../utils/notify_advenced";
import { useNavigate } from 'react-router-dom';
import { Typography } from "@mui/material";
import SingleCustomer from "../../customer/singleCustomer/singleCustomer";
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';

function GetAllCustomers(): JSX.Element {
    const [customers,SetData]= useState<Customer[]>([])
    const [foundCustomer,setFind]=useState("");
    const search=(sender: SyntheticEvent)=>{
        const value = (sender.target as HTMLInputElement).value;
        setFind(value);
        }
        const found= ()=>{
        setFind(foundCustomer);
        }

const navigate= useNavigate();
    useEffect(()=>{
        if(store.getState().authState.userType != 'ADMIN'){
            advNotify.error(ErrMsg.LOGIN_FIRST)
            navigate("/login");
        }
        SetData(store.getState().customerState.customers)

    },[])
    return (
        <div className="getAllCustomers">
        <div style={{textAlign: "center"}}>
    <Typography variant="h5" gutterBottom component="div"  color={"primary"} >Customers in The System</Typography>
    <Paper 
    component="form"
    sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: 400 ,textAlign:'center'}}
    >
    <IconButton sx={{ p: '10px' }} aria-label="menu">
    <MenuIcon />
    </IconButton>
    <InputBase
    onChange={search} value={foundCustomer}
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search Customer"
        inputProps={{ 'aria-label': 'search' }}
    />
    <IconButton type="button" onClick={found} value={foundCustomer} sx={{ p: '10px' }} aria-label="search">
    <SearchIcon />
    </IconButton>
    <Divider sx={{ height: 28, m: 0.5 }} orientation="vertical" />
    </Paper>
    </div>
    <hr />

        {customers.filter(item=>item.firstName.match(foundCustomer)).map(item=><SingleCustomer key={item.id} customerData={item}/>)}
        </div>
    );
}

export default GetAllCustomers;
