import { Box, Button,Card, ButtonGroup,  Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Typography } from "@mui/material";
import { borderRadius } from "@mui/system";
import Coupon from "../../../modal/Coupon";
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import "./singleCoupon.css";
import EditIcon from "@mui/icons-material/Edit";
import { useNavigate } from "react-router-dom";
import { useCallback, useState } from "react";
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import jwtAxios from "../../../utils/JWTaxios";
import globals from "../../../utils/global";
import advNotify, { ErrMsg, SccMsg } from "../../../utils/notify_advenced";
import notify from "../../../utils/notify";
import { store } from "../../../redux/store";
import { addCoupon, deleteCoupon } from "../../../redux/couponState";
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton, { IconButtonProps } from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { blue, red } from '@mui/material/colors';
import { purchaseCoupon } from "../../../redux/customerState";
import Customer from "../../../modal/Customer";
import { updateAmount } from "../../../redux/guestState";
interface SingleCouponProps {
	couponData: Coupon
}

function SingleCoupon(props: SingleCouponProps): JSX.Element {
    const [open, setOpen] = useState(false);
    const navigate=useNavigate();
    let [amountValue,setAmount] = useState(props.couponData.amount);
    var startDate = new Date().toISOString().slice(0, 10);
    const amountPurchase=()=>{
    setAmount(amountValue-1);
    } 
//open dialog
    const handleClickOpen = () => {
    setOpen(true);
    };
//close dialog
    const handleClose = () => {
    setOpen(false);
    };

        const purchase=()=>{
        if(store.getState().authState.userType=='CUSTOMER'){
            if(props.couponData.amount>0){
            let customer :Customer=store.getState().customerState.customers[0];
            //JSON.stringify(obj1) === JSON.stringify(obj2) 
        if(JSON.stringify(customer.coupons?.find(item=>item.id == props.couponData.id)) != JSON.stringify(props.couponData)){
        jwtAxios.post(globals.customer.purchaseCoupon+props.couponData.id)
        .then((response=>{
            if(response.status==200){
                amountPurchase()
               // props.couponData.amount = amountValue;;
                store.dispatch(updateAmount(response.data));
                store.dispatch(purchaseCoupon(response.data));
                console.log(props.couponData.amount);
                console.log(props.couponData);
                advNotify.success(SccMsg.PURCHASE_COUPON)
            }else{
                notify.error("Something went wrong");
            }
            }))
            .catch((err)=>{
            console.log(err);
            })
            }else{
            advNotify.error(ErrMsg.PURCHASE_COUPON_ERROR);
            return;
            }
            }
            }
            else{
            advNotify.error(ErrMsg.LOGIN_FIRST);
            navigate("/login");
            }
        }
        
    const removeCoupon= ()=>{
    handleClose();
    console.log(props.couponData.id);
    jwtAxios.delete(globals.company.deleteCoupon+props.couponData.id)
    .then((response)=>{
    if(response.status==200){
        advNotify.success("Coupon number "+props.couponData.id+" "+SccMsg.DELETE_COUPON);
    }else{
        notify.error("Something went wrong")
    }
    })
    .then(()=>{
        store.dispatch(deleteCoupon(props.couponData.id));
        navigate("/company/companyCoupons",{state:{companyId:0}})
    })
    .catch((err)=>{
    console.log(err);
    })
    }
   

    const cardStyle = {
        display: "block",
        transitionDuration: "0.3s",
        height: "520px"
    };
    return (
        <div className="singleCoupon Single" style={{ textAlign: "center" }}> 
        <Card style={cardStyle} elevation={6} sx={{ maxWidth: 320 }}>
        <CardHeader
        avatar={
        <Avatar sx={{ bgcolor: blue[500] }} aria-label="recipe">
        {props.couponData.id}
        </Avatar>
        }
        action={
        <IconButton aria-label="add to favorites">
        <FavoriteIcon />
        </IconButton>
        }
        title={props.couponData.category}
        />
        <CardMedia
        component="img"
        height="194"
        image={props.couponData.image} 
        alt="Paella dish"
        />
        <CardContent>
        <Typography gutterBottom variant="h5" component="div">
        {props.couponData.title}
        </Typography>
        <Typography variant="body1" color="text.secondary" fontWeight="bold">
        {props.couponData.description}
        </Typography>
        <Typography variant="body1" > &nbsp; &nbsp; price: {props.couponData.price} </Typography>

        { (props.couponData.amount<5 && props.couponData.amount>1)&&
        <Typography variant="body1" color='error' > &nbsp; &nbsp; Last Items in Stock </Typography>}
        { (props.couponData.amount==0)&&
        <Typography variant="body1" color='error' > &nbsp; &nbsp; Out Of Stock </Typography>}
        {(props.couponData.endDate< startDate )&&   <Typography variant="h6" color='error' >Expires!!!!</Typography> }
        {(props.couponData.endDate> startDate )&&<Typography variant="h6" color='error' >Expires: {props.couponData.endDate}</Typography>}
        </CardContent>
        <CardActions disableSpacing>
        {(store.getState().authState.userType=='CUSTOMER' || store.getState().authState.userType=='GUEST') &&(props.couponData.amount!=0) &&(props.couponData.endDate>startDate ) && <IconButton onClick={purchase}>
        <AddShoppingCartIcon />
        </IconButton>}
        <ButtonGroup>
            {(store.getState().authState.userType=='COMPANY') && <Button   startIcon={<EditIcon style={{minWidth: '30px'}} />}
            size="small"
            type="submit"
            color="info"
            variant="contained"
            onClick={() => {
            navigate("/company/updateCoupon", {
                state:{ couponId: props.couponData.id }
            });
            }}
            >
            Update 
            </Button>}
            </ButtonGroup>
            &nbsp; &nbsp;
            {(store.getState().authState.userType=='COMPANY') && <Button  
            startIcon={<HighlightOffIcon style={{minWidth: '30px'}}/>}
            size="small"
            type="submit"
            color="error"
            variant="contained"
            onClick={handleClickOpen}
            >
            Delete
            </Button>}
            <Dialog
            open={open}
            onClose={handleClose}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            >
            <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to delete this coupon?"}
            </DialogTitle>
            <DialogContent>
            <DialogContentText id="alert-dialog-description">
            Delete this coupon will delete all Customers's coupons History
            </DialogContentText>
            </DialogContent>
            <DialogActions></DialogActions>
            <DialogActions>
               
            <Button onClick={handleClose}>Disagree</Button>
            <Button onClick={ removeCoupon} >

            Agree
            </Button>
           
            </DialogActions>
            </Dialog>
        </CardActions>
        </Card>

        </div>
    );
}

export default SingleCoupon;
