import { Button, IconButton, Typography } from "@mui/material";
import { NavLink, useNavigate } from "react-router-dom";
import "./page404.css";
import HomeIcon from '@mui/icons-material/Home';


function Page404(): JSX.Element {
    const navigate=useNavigate();
    document.addEventListener("DOMContentLoaded",function(){
    var screen=screen.height as any;
        var body=document.body;
            setInterval(createStar,100);
            function createStar(){
            var right=Math.random()*500;
            var top=Math.random()*screen;
            var star=document.createElement("div");
            star.classList.add("star")
            body.appendChild(star);
            setInterval(runStar,10);
            star.style.top=top+"px";
            function runStar(){
            if(right>=screen.width){
                star.remove();
            }
            right+=3;
            star.style.right=right+"px";
            }
            } 
            })

    return (

        <div className="page404">
            <body className="page404">
            <div className="text">
            <div>ERROR</div>
            <h1>404</h1>
            <hr/>
            <div>Page Not Found</div>
            </div>
            <div className="astronaut">
            <img src="https://images.vexels.com/media/users/3/152639/isolated/preview/506b575739e90613428cdb399175e2c8-space-astronaut-cartoon-by-vexels.png" alt="" className="src"/>
            </div> 
            <IconButton onClick={()=>{navigate("/")}} ><HomeIcon/></IconButton>
            </body>
            </div>
    );
}

export default Page404;
